import React from 'react';
import PropTypes from 'prop-types';

const Navbar = (props) => {
        return (
            <nav className='navbar bg-primary'>
                <h1>
                    <i className ={props.icon}/>   {props.title}
                </h1>
                    <ul>
                        <li>
                            <a href='/about'>About</a>
                        </li>
                    </ul>
            </nav>
        )
    }

Navbar.defaultProps ={
    title :'Github Finder',
    icon :'fab fa-github'
}


export default Navbar
